
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  width: 100%;
  height: 100%;
}
#app .arrow {
  position: absolute;
  bottom: 1rem;
  left: 50%;
  margin-left: -1.75rem;
  -webkit-animation: arrow 1.5s infinite;
          animation: arrow 1.5s infinite;
  z-index: 1000;
  width: 4.5rem;
  height: 2rem;
  font-size: 0.7rem;
  text-align: center;
  color: #fff;
}
#app .arrow p {
  line-height: 1.5rem;
}
#app .arrow .iconfont {
  padding: 0;
  color: #000;
  color: #fff;
}
#app .child-view {
  transition: all 0.8s;
  position: absolute;
}
#app .rotate-enter {
  -webkit-transform: rotate(480deg) scale(0);
          transform: rotate(480deg) scale(0);
  opacity: 0;
}
#app .rotate-leave-to {
  opacity: 0;
  -webkit-transform: scale(2);
          transform: scale(2);
}
#app .rotate-enter-to,
#app .rotate-leave {
  -webkit-transform: rotate(0deg) scale(1);
          transform: rotate(0deg) scale(1);
  opacity: 1;
}
@-webkit-keyframes arrow {
from {
    bottom: 1.5rem;
    opacity: 1;
}
to {
    bottom: 1rem;
    opacity: 0.2;
}
}
@keyframes arrow {
from {
    bottom: 1.5rem;
    opacity: 1;
}
to {
    bottom: 1rem;
    opacity: 0.2;
}
}


