
.skill {
  width: 100%;
  height: 100%;
  background: #87a461;
}
.skill .content {
  height: 24rem;
  position: relative;
  top: 50%;
  margin-top: -12rem;
}
.skill .content .title {
  margin-top: 1.5rem;
  font-size: 2rem;
  text-shadow: 0.1rem 0.15rem #22c3aa, 0.15rem 0.2rem #aee2d9;
  font-weight: 800;
  color: #fff;
}
.skill .content .body {
  height: 80%;
}
.skill .content .body .basic {
  width: 100%;
  height: 100%;
}


