
.about {
  width: 100%;
  height: 100%;
  background: #857578;
}
.about .content {
  height: 24rem;
  position: relative;
  top: 50%;
  margin-top: -12rem;
}
.about .content .title {
  margin: 1rem;
  font-size: 2rem;
  text-shadow: 0.1rem 0.15rem #857578, 0.15rem 0.2rem #aee2d9;
  font-weight: 800;
  color: #fff;
}
.about .content .download {
  text-shadow: 0.1rem 0.15rem #857578, 0.15rem 0.2rem #aee2d9;
  font-size: 1.5rem;
  color: #fff;
  text-decoration: none;
}
.about .content .download:hover {
  color: #839acc;
}
.about .content .body .title {
  margin-top: 1rem;
  font-size: 1.5rem;
}
.about .content .body ul:last-child {
  margin-top: 1.5rem;
}
.about .content .body ul:last-child li {
  display: inline-block;
  margin: 0.5rem;
}
.about .content .body ul .infos {
  list-style: none;
  color: rgba(255,255,255,0.5);
  font-size: 0.7rem;
  line-height: 1.5rem;
}
.about .content .body ul .infos .link {
  display: inline-block;
  text-decoration: none;
}
.about .content .body ul .infos .link .iconfont {
  padding: 0;
  font-size: 1.6rem;
  color: rgba(255,255,255,0.6);
}
.about .content .body ul .infos .link .iconfont:hover {
  color: #839acc;
}


