
.project {
  width: 100%;
  height: 100%;
  background: #fd851c;
}
.project .content {
  height: 24rem;
  position: relative;
  top: 50%;
  margin-top: -12rem;
}
.project .content .title {
  margin: 1rem;
  font-size: 2rem;
  text-shadow: 0.1rem 0.15rem #fd851c, 0.1rem 0.2rem #aee2d9;
  font-weight: 800;
  color: #fff;
}
.project .content .body {
  height: 80%;
}
.project .content .body ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.project .content .body ul li {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  list-style: none;
  color: rgba(255,255,255,0.6);
}
.project .content .body ul li .project-name {
  font-size: 1rem;
  color: rgba(255,255,255,0.8);
  text-decoration: none;
  line-height: 2rem;
}
.project .content .body ul li .project-name:hover {
  color: #9f3;
}
.project .content .body ul li .des {
  font-size: 0.7rem;
}


